.main-fbody{
    height: 100%;
    background-image: linear-gradient(to right, #6495ED, #AED6F1 , #D7BDE2, #AF7AC5);
    background-size:cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .bea-text{
    color: black;
    font-size: 10vw; /* Responsive font size */
    font-weight: bold;
    margin: 0 auto;
    text-align: center; /* Center text */
    mix-blend-mode:overlay; /* This makes the cutout text possible */ 
  
    position: relative;
    display: inline-block;
  }
  
  .bea-popup {
    font-size: x-large;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  
  .bea-text .bea-popup{
    visibility: hidden;
    width: fit-content;
    background-color: white;
    color: #555;
    text-align: center;
    padding: 3%;
    border-radius: 3rem;
  
    /* Position the tooltip text */
    position: absolute;
    z-index: -1;
    bottom: 80%;
    left: 90%;
  
    /* Fade in tooltip */
    opacity: 0;
    transition: opacity 0.3s;
  }
  
  .bea-text .bea-popup::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 20%;
    margin-left: -5px;
    border-width: 10px;
    border-style: solid;
    border-color: white transparent transparent transparent;
  }
  
  .bea-text:hover .bea-popup{
    visibility: visible;
    opacity: 1;
  }
  
  .img-popup{
    width: 4rem;
    height: 4rem;
    margin-left: 1rem;
  }
  
  .add-text{
    padding: 1rem;
    position: inherit;
    font-family: 'Times New Roman', Times, serif;
    color: rgba(85, 85, 85, 0.5);
  }
  
  .nav-opt-bg{
    border-radius: 5rem !important;
    transition-delay: .1s!important;
    transition: all .6s;
    transition: opacity 0.3s;
    -webkit-transition: opacity 0.3s;
  }
  
  .nav-space {
    display: flex;
    flex-direction: row;
    justify-content: center;
    list-style-type: none;
    position: relative;
    margin: 0;
    padding: 0;
    opacity: 1;
    top: 0;
    transition: opacity 0.3s;
    -webkit-transition: opacity 0.3s;
    transform: none !important;
    transition-delay: .1s!important;
    transition: all .6s;
    height: fit-content;
    width: 100%;
    pointer-events: none;
  }
  
  .nav-opt{
    display: flex;
    justify-content: center;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, 0.315);
    width: 4rem;
    height: 4rem;
    margin: 1rem;
    list-style: none;
    transition: .3s; 
    pointer-events: auto;
  }
  
  i{
    position: absolute;
   font-size: 40px;
  }
  
  a{
    pointer-events: none;
    text-decoration: none;
  }
  
  .nav-info {
    position: absolute;
    top: 0%;
    padding: 2% 5%;
    opacity: 0;
    font-size: larger;
    z-index: 3;
    /* border-radius: 5rem; */
  }

  .school-info {
    left: 50%;
  }
  
  .nav-opt-bg:hover, .nav-opt:hover .nav-info{
    opacity: 1;
    left: 0%;
    top: 90%;
    text-align: center;
    background: white;
    transition: all .5s;
  }

  .nav-opt-bg:hover, .nav-opt:hover .nav-info .cert-info .cert-space{
    margin: 0 3rem;
  }

  .prof-head, .work-head, .school-head, .proj-head {
    font-weight: bold;
  }

  .work-sub, .school-sub, .proj-sub{
    font-style: italic;
  }

  .school-smoler, .cert-smol{
    font-size: medium;
  }

  .cap{
    font-weight: 700;
    font-style: oblique;
  }

  .cert-info{
    /* border: 2px solid yellow; */
    /* width: 100%; */
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    /* margin: 0 5rem; */
  }

  .cert-space{
    /* border: 2px solid gray; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* margin: 0 2.5%; */
  }