.my-cards {
    background-color: transparent;
    width: 300px;
    height: 25000%;
    border: 1px solid black;
    perspective: 1000px; /* Remove this if you don't want the 3D effect */
    border-radius: 5rem;
    margin: 0 0.5rem;
  }
  
  /* This container is needed to position the front and back side */
  .my-cards-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
  }
  
  /* Do an horizontal flip when you move the mouse over the flip box container */
  .my-cards:hover .my-cards-inner {
    transform: rotateY(180deg);
  }
  
  /* Position the front and back side */
  .front-card, .back-card {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
  }
  
  /* Style the front side (fallback if image is missing) */
  .front-card {
    background-color: white;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5rem;
  }

  .front-cont {
      margin: 2rem;
  }
  
  /* Style the back side */
  .back-card {
    background-color: #f3f3f5;
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: rotateY(180deg);
    border-radius: 5rem;
  }

  .back-cont{
      margin: 2rem;
  }

  .card-img{
      width: 100%;
      height: 100%;
      /* margin-bottom: 1rem; */
  }

  p{
      font-size: small;
  }

  .title-space {
    /* border: 2px solid red; */
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 1rem;
    padding: 1rem;
  }

  .title-text, .title-text2, .title-text3{
      /* border: 2px solid green; */
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: xx-large;
      font-weight: bolder;
  }

  .title-text{
    margin-top: -5%;
  }

  .title-text3{
    margin-top: -5%;
  }
